.status-dropdown {
  .custom-input {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      // background: url(../assets/images/editIcon-theme.png);
      background: url(../../../assets/images/editIcon-theme.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      height: 16px;
      width: 16px;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      pointer-events: none;
    }
  }
}